@media screen and (max-width: 662px) {
  .order__button {
    margin-bottom: 40px; }

  .order__acard {
    width: 100%; }

  .order__text {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px; }

  .order__wrap {
    flex-wrap: wrap; } }

@media (max-width: 360px) {
  .order__table-wrap {
    overflow: scroll; } }


.order__table TD, TH {
  font-size: 17px;
  font-family: 'FuturaPT', Arial, sans-serif;
  padding: 10px;
  border: 1px solid black; }
