.bank {
  background: url(../img/bg2.jpg) no-repeat center / cover;
  padding: 150px 0px;
  @media (max-width: 600px) {
    padding: 50px 0px; }
  &__wrap {
    max-width: 700px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center; }
  &__numbers {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-bottom: 30px;
    @media (max-width: 600px) {
      flex-direction: column;
      align-items: center;
      justify-content: center; } }
  &__number {
    text-align: center;
    font-size: 20px;
    @media (max-width: 600px) {
      margin-bottom: 30px; } }
  &__numeric {
    font-size: 80px;
    font-weight: 800;
    margin-bottom: 10px; }
  &__row {
    width: 100%;
    margin: 0 auto;
    padding: 20px 35px;
    background-color: #fff705;
    color: #000000;
    font-size: 35px;
    font-family: 'FuturaPT';
    line-height: 1.55;
    font-weight: 700;

    text-align: center;
    margin-bottom: 20px;
    @media (max-width: 600px) {
      line-height: 1; } }
  &__text {
    font-size: 20px;
    margin-bottom: 45px; }

  &__images {

    width: 100%;
    display: flex;
    justify-content: space-around;
    @media (max-width: 600px) {
      flex-direction: column;
      align-items: center;
      justify-content: center; } }
  &__img {
    width: 200px;
    @media (max-width: 600px) {
      width: 100%;
      margin-bottom: 30px; } } }
