.akciaModal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  background: rgba(0,0,0, .5);
  display: none;
  &__wraper {
    position: relative; }
  &__img {
    animation: showAkcia 1s;
    max-width: 800px;
    @media (max-width: 700px) {
      padding: 0px 20px; } }
  &__close {
    position: absolute;
    top: -7px;
    right: -28px;
    @media (max-width: 860px) {
      top: -40px;
      right: 28px; }
    // @media (max-width: 400px)
    //   top: 253px
 } }    //   right: 16px

@keyframes showAkcia {
  0% {
    opacity: 0;
    transform: scale(0); }
  100% {
    opacity: 1;
    transform: scale(1); } }


