.msgrow {
  max-width: 960px;
  margin-left:  auto;
  margin-right: auto;
  line-height: 1.55;
  margin-bottom: 40px;
  &__title {
    font-size: 35px;
    font-weight: 700;
    @media (max-width: 600px) {
      font-size: 30px; } }
  &__text {
    font-size: 20px;
    font-weight: 400; } }

.important {
