.header {
  height: 85px;
  display: flex;
  background: black;

  position: fixed;
  width: 100%;
  font-weight: 400;
  font-family: 'FuturaPT', Arial, sans-serif;
  z-index: 10;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%; }
  &__logo-block {
    display: flex;
    align-items: center; }
  &__logo {
    width: 140px;
    margin-right: 10px;
    @media (max-width: 460px) {
      width: 90px; } }
  &__logo-text {
    color: white;
    width: 190px;
    font-size: 15px;
    @media (max-width: 770px) {
      display: none; } }
  &__contact-block {
    display: flex;
    align-items: center; }
  &__contact-text {

    line-height: 1.5;
    color: #c7c7c7;
    margin-right: 30px;
    @media (max-width: 360px) {
      line-height: 1.2;
      font-size: 12px; } }

  &__phone {

    display: block;
    color: #ffffff;
    font-size: 24px;
    font-weight: 900;
    line-height: 1.55;
    @media (max-width: 460px) {
      font-size: 18px; }
    @media (max-width: 360px) {
      font-size: 14px; } }
  &__contact-socicon {
    @media (max-width: 460px) {
      display: none; } }
  &__icon {
    width: 40px; }
  &__menu-link {
    background: none;
    border: none;
    display: block;
    appearance: none;
    width: 40px;
    transform: translateX(40px);
    @media (max-width: 583px) {
      transform: translateX(0px); } } }


